@import url("https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  overflow-y: hidden;
 user-select: none;
  font-family: "Press Start 2P", cursive;
}
.main{
    user-select: none;
}
.header {
  width: 100vw;
  height: 100px;
  background-color: blueviolet;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 5px 5px 10px 5px black;
  /* margin-bottom: 1rem; */
  border-radius: 2px;
}
.header > h1 {
  color: aliceblue;
  font-size: 3rem;
  position: relative;
  bottom: 5%;
}
.header > input {
  position: relative;
  left: 30%;
  padding: 1rem;
  border-radius: 8px;
}
.pokemon {
  display: flex;
  flex-wrap: wrap;
  margin: 2rem;
  justify-content: center;
  overflow-y: scroll;
  height: 75vh;
  border: 2px solid transparent;
  box-shadow: 5px 5px 10px 5px black;
  border-radius: 8px;
}
.pokemon::-webkit-scrollbar {
  display: none;
}
@media (min-width: 100px) and (max-width: 1090px) {
  .header {
    display: flex;
    flex-direction: column;
    /* width: fit-content; */
    height: 25vh;
  }
  .header > input {
    position: relative;
    left: 1%;
    bottom: -5%;
  }
  .header > h1 {
    position: relative;
    top: -10%;
  }
}
