@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  overflow-y: hidden;
 -webkit-user-select: none;
     -ms-user-select: none;
         user-select: none;
  font-family: "Press Start 2P", cursive;
}
.main{
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.header {
  width: 100vw;
  height: 100px;
  background-color: blueviolet;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 5px 5px 10px 5px black;
  /* margin-bottom: 1rem; */
  border-radius: 2px;
}
.header > h1 {
  color: aliceblue;
  font-size: 3rem;
  position: relative;
  bottom: 5%;
}
.header > input {
  position: relative;
  left: 30%;
  padding: 1rem;
  border-radius: 8px;
}
.pokemon {
  display: flex;
  flex-wrap: wrap;
  margin: 2rem;
  justify-content: center;
  overflow-y: scroll;
  height: 75vh;
  border: 2px solid transparent;
  box-shadow: 5px 5px 10px 5px black;
  border-radius: 8px;
}
.pokemon::-webkit-scrollbar {
  display: none;
}
@media (min-width: 100px) and (max-width: 1090px) {
  .header {
    display: flex;
    flex-direction: column;
    /* width: fit-content; */
    height: 25vh;
  }
  .header > input {
    position: relative;
    left: 1%;
    bottom: -5%;
  }
  .header > h1 {
    position: relative;
    top: -10%;
  }
}

.card {
  border: 2px solid transparent;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 20rem;
  width: 27vw;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  transition: all 2s ease;
  border-radius: 8px;
  box-shadow: 5px 5px 10px 10px blueviolet;
  color: blueviolet;
}
.card:hover{
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}

img {
  width: 200px;
  position: relative;
  top: -35%;
  height: 150px;
}
.container {
  display: flex;
  justify-content: center;
  margin: 1rem;
}
.front {
  display: flex;
  flex-direction: column;
  align-items: center;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  position: relative;
  top: 10%;
}
.back {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  position: absolute;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  bottom: 5%;
  line-height:  40px;
  /* margin: 5rem; */
  right: 10%;
  font-size: 1.5rem;

}
.name {
  font-size: 2rem;
  color: blueviolet;
  text-transform: capitalize;
}
  @media (min-width:100px) and (max-width:1090px){
    .back {
      -webkit-transform: rotateY(180deg);
              transform: rotateY(180deg);
      position: absolute;
      -webkit-backface-visibility: hidden;
              backface-visibility: hidden;
      bottom: 10%;
      line-height:  40px;
      width: 60vw;
      right: 1%;
      font-size: 1.5rem;
      display: flex;
      flex-wrap: wrap;
    }
        
    
}
